const modalsBtns = document.querySelectorAll('.modals-btns');
const modals = document.querySelectorAll('.modals');
const modalOverlay = document.querySelectorAll('.modal-overlay');
const modalClose = document.querySelectorAll('.modal-close');

modalsBtns.forEach((el) => {
  el.addEventListener('click', (e) => {
    modalOverlay.forEach((item) => {
      item.classList.add('modal-overlay--visible');
    });

    document.body.classList.add('scroll-lock');

    let path = e.currentTarget.getAttribute('data-path');
    document.querySelector(`[data-target="${path}"]`).classList.add('modals--visible');
  });
});

modalClose.forEach((close) => {
  close.addEventListener('click', (closeEl) => {
    modalOverlay.forEach((item) => {
      item.classList.remove('modal-overlay--visible');
    });

    document.body.classList.remove('scroll-lock');

    let dataClose = closeEl.currentTarget.getAttribute('data-close');
    document.querySelector(`[data-target="${dataClose}"]`).classList.remove('modals--visible');
  });
});