const accItem = document.querySelectorAll('.account__item');
const accTab = document.querySelectorAll('.account-tab');

accItem.forEach((item) => {
  item.addEventListener('click', () => {
    let accItemId = item.getAttribute('data-tab');
    let accBtnId = document.querySelector(accItemId);

    accTab.forEach((item) => {
      item.classList.remove('tab-active');
    });

    accBtnId.classList.add('tab-active');
  });
});