const periodBtn = document.querySelectorAll('.period__meaning');
const card = document.querySelectorAll('.card');

periodBtn.forEach((item) => {
  item.addEventListener("click", () => {
    let btnId = item.getAttribute('data-card');
    let currentBtnCard = document.querySelector(btnId);

    periodBtn.forEach((item) => {
      item.classList.add('no-active');
    });

    card.forEach((item) => {
      item.classList.remove('card-tab--active');
    });

    item.classList.remove('no-active');
    currentBtnCard.classList.add('card-tab--active');
  });
});